.flex-direction-nav {
    margin: 0;
    padding: 0;
    list-style: none;
    border: none;
}

.flexslider {
    position: relative;
    margin: 0;
    padding: 0;
}

.flexslider .slider-wrap > .slide {
    display: none;
    -webkit-backface-visibility: hidden;
}

.flexslider .slider-wrap img {
    width: 100%;
    display: block;
}

.flex-pauseplay span {
    text-transform: capitalize;
}

.slider-wrap:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
}

