}

#side-panel-trigger {
    display: block;
    cursor: pointer;
    z-index: 11;
    margin-right: 5px;
}

#side-panel-trigger-close a {
    display: none;
}

#side-panel,
body.side-push-panel.stretched #wrapper,
body.side-push-panel.stretched #header .container {
    -webkit-transition: right .4s ease;
    transition: right .4s ease;
}

body.side-panel-left #side-panel,
body.side-panel-left.side-push-panel.stretched #wrapper,
body.side-panel-left.side-push-panel.stretched #header .container,
body.side-push-panel.stretched .slider-parallax-inner {
    -webkit-transition: left .4s ease;
    transition: left .4s ease;
}

.body-overlay {
    opacity: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: -1;
    pointer-events: none;
    -webkit-backface-visibility: hidden;
    -webkit-transition: opacity .3s ease;
    transition: opacity .3s ease;
}

body.side-panel-open .body-overlay {
    opacity: 1;
    z-index: 298;
    pointer-events: auto;
}

body.side-panel-open:not(.device-xs):not(.device-sm):not(.device-md) .body-overlay:hover {
    cursor: url('/assets/images/icons/close.png') 15 15, default;
}

/* ----------------------------------------------------------------
	Slider
-----------------------------------------------------------------*/

.slider-element {
    position: relative;
    width: 100%;
    overflow: hidden;
}

.slider-parallax {
    position: relative;
}

.slider-parallax,
.slider-parallax .swiper-container {
    visibility: visible;
    -webkit-backface-visibility: hidden;
}

.slider-parallax .slider-parallax-inner {
    position: fixed;
    width: 100%;
    height: 100%;
}

.device-touch .slider-parallax .slider-parallax-inner {
    position: relative;
}

body:not(.side-header) .slider-parallax .slider-parallax-inner {
    left: 0;
}

body:not(.stretched) .slider-parallax .slider-parallax-inner {
    left: auto;
}

#header.transparent-header + .slider-parallax .slider-parallax-inner {
    top: 0;
}

.slider-parallax .slider-parallax-inner,
.slider-parallax .slider-caption {
    will-change: transform;
    -webkit-transform: translate3d(0, 0, 0);
    -webkit-transition: transform 0s linear;
    -webkit-backface-visibility: hidden;
}

/* Boxed Slider
-----------------------------------------------------------------*/

.boxed-slider {
    padding-top: 40px;
}

/* Flex Slider - Thumbs
-----------------------------------------------------------------*/

.fslider[data-animation="fade"][data-thumbs="true"] .flexslider,
.fslider.testimonial[data-animation="fade"] .flexslider {
    height: auto !important;
}

.flex-control-nav.flex-control-thumbs {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    margin: 2px -2px -2px 0;
    height: 75px;
}

.flex-control-nav.flex-control-thumbs li {
    margin: 0 2px 2px 0;
}

.flex-control-nav.flex-control-thumbs li img {
    cursor: pointer;
    text-indent: -9999px;
    border: 0;
    border-radius: 0;
    margin: 0;
    opacity: 0.5;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.flex-control-nav.flex-control-thumbs li,
.flex-control-nav.flex-control-thumbs li img {
    display: block;
    width: 100px !important;
    height: auto !important;
}

.flex-control-nav.flex-control-thumbs li:hover img,
.flex-control-nav.flex-control-thumbs li img.flex-active {
    border-width: 0;
    opacity: 1;
}

/* Flex Thumbs - Flexible
-----------------------------------------------------------------*/

.fslider.flex-thumb-grid .flex-control-nav.flex-control-thumbs {
    margin: 2px -2px -2px 0;
    height: auto;
}

.fslider.flex-thumb-grid .flex-control-nav.flex-control-thumbs li {
    width: 25% !important;
    height: auto !important;
    margin: 0;
    padding: 0 2px 2px 0;
}

.fslider.flex-thumb-grid.grid-3 .flex-control-nav.flex-control-thumbs li {
    width: 33.30% !important;
}

.fslider.flex-thumb-grid.grid-5 .flex-control-nav.flex-control-thumbs li {
    width: 20% !important;
}

.fslider.flex-thumb-grid.grid-6 .flex-control-nav.flex-control-thumbs li {
    width: 16.66% !important;
}

.fslider.flex-thumb-grid.grid-8 .flex-control-nav.flex-control-thumbs li {
    width: 12.5% !important;
}

.fslider.flex-thumb-grid.grid-10 .flex-control-nav.flex-control-thumbs li {
    width: 10% !important;
}

.fslider.flex-thumb-grid.grid-12 .flex-control-nav.flex-control-thumbs li {
    width: 8.33% !important;
}

.fslider.flex-thumb-grid .flex-control-nav.flex-control-thumbs li img {
    width: 100% !important;
    height: auto !important;
}

/* Swiper Slider
-----------------------------------------------------------------*/

.swiper_wrapper {
    width: 100%;
    height: 500px;
    overflow: hidden;
}

.full-screen .swiper_wrapper {
    height: auto;
}

.swiper_wrapper .swiper-container {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0;
    background: url('/assets/images/pattern2.png') repeat;
}

.swiper_wrapper .swiper-slide {
    position: relative;
    margin: 0;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}

.swiper-slide [data-caption-animate] {
    -webkit-backface-visibility: hidden;
}

.swiper-slide .video-wrap,
.section .video-wrap,
.swiper-slide .yt-bg-player,
.section .yt-bg-player {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    top: 0;
    left: 0;
}

.slider-element .container {
    position: relative;
    height: 100%;
}

.slider-element.canvas-slider .swiper-slide img {
    width: 100%;
}

.slider-element.canvas-slider a {
    display: block;
    height: 100%;
}

.swiper-pagination {
    position: absolute;
    width: 100%;
    z-index: 20;
    margin: 0;
    top: auto;
    bottom: 20px !important;
    text-align: center;
    line-height: 1;
}

.swiper-pagination span {
    display: inline-block;
    cursor: pointer;
    width: 10px;
    height: 10px;
    margin: 0 4px;
    opacity: 1;
    background-color: transparent;
    border: 1px solid #FFF;
    border-radius: 50%;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.swiper-pagination span:hover,
.swiper-pagination span.swiper-pagination-bullet-active {
    background-color: #FFF !important;
}

/* Revolution Slider
-----------------------------------------------------------------*/

.tp-banner .button {
    height: auto !important;
}

/* Video Overlay
-----------------------------------------------------------------*/

.video-wrap {
    overflow: hidden;
    width: 100%;
    position: relative;
}

.video-wrap video {
    position: relative;
    z-index: 1;
    width: 100%;
}

.video-overlay,
.video-placeholder {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 5;
    background: transparent url('/assets/images/grid.png') repeat;
    -webkit-backface-visibility: hidden;
}

.video-placeholder {
    z-index: 4;
    background-repeat: no-repeat !important;
}

/* Slider Caption
-----------------------------------------------------------------*/

.slider-caption,
.flex-caption {
    position: absolute;
    top: 0;
    left: 15px;
    z-index: 20;
    max-width: 550px;
    color: #EEE;
    font-size: 1.375rem;
    font-weight: 300;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    -webkit-transition: top .3s ease;
    transition: top .3s ease;
}

.slider-caption:not(.dark),
.slider-caption:not(.dark) h2 {
    color: #222;
    text-shadow: none;
}

.slider-caption h2 {
    font-size: 4rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 0;
}

.slider-caption p {
    margin-top: 30px;
    margin-bottom: 10px;
    font-size: 24px;
}

.slider-caption.slider-caption-right {
    left: auto;
    right: 15px;
}

.slider-caption.slider-caption-center {
    position: relative;
    max-width: 800px;
    text-align: center;
    margin: 0 auto;
    left: 0;
    right: 0;
}

.slider-caption-bg {
    padding: 5px 12px;
    border-radius: 2px;
    background-color: rgba(0, 0, 0, 0.6);
    top: auto;
    bottom: 50px;
    left: 50px;
}

.slider-caption-bg.slider-caption-bg-light {
    background-color: rgba(255, 255, 255, 0.8);
    color: #333;
    font-weight: 400;
    text-shadow: none;
}

.slider-caption-top-left {
    top: 50px;
    bottom: auto;
}

.slider-caption-top-right {
    top: 50px;
    bottom: auto;
    left: auto;
    right: 50px;
}

.slider-caption-bottom-right {
    left: auto;
    right: 50px;
}

/* Slider Arrows
-----------------------------------------------------------------*/

.slider-element .owl-carousel {
    margin: 0;
}

.slider-arrow-left,
.slider-arrow-right,
.flex-prev,
.flex-next,
.slider-arrow-top-sm,
.slider-arrow-bottom-sm {
    position: absolute;
    cursor: pointer;
    z-index: 10;
    top: 50%;
    left: 0;
    margin-top: -26px;
    background-color: rgba(0, 0, 0, 0.3);
    width: 52px;
    height: 52px;
    border: 0;
    border-radius: 0 3px 3px 0;
    -webkit-transition: background-color .3s ease-in-out;
    transition: background-color .3s ease-in-out;
}

.slider-arrow-top-sm,
.slider-arrow-bottom-sm {
    top: auto;
    bottom: 57px;
    left: auto;
    right: 20px;
    margin: 0;
    width: 32px;
    height: 32px;
    border-radius: 3px 3px 0 0;
}

.slider-arrow-bottom-sm {
    bottom: 20px;
    border-radius: 0 0 3px 3px;
}

.fslider[data-thumbs="true"] .flex-prev,
.fslider[data-thumbs="true"] .flex-next {
    margin-top: -64px;
}

.slider-arrow-right,
.slider-element .owl-next,
.flex-next {
    left: auto;
    right: 0;
    border-radius: 3px 0 0 3px;
}

.slider-arrow-left i,
.slider-arrow-right i,
.flex-prev i,
.flex-next i,
.slider-arrow-top-sm i,
.slider-arrow-bottom-sm i {
    line-height: 50px;
    width: 34px;
    height: 52px;
    color: #DDD;
    color: rgba(255, 255, 255, 0.8);
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
    font-size: 34px;
    text-align: center;
    margin-left: 7px;
}

.slider-arrow-top-sm i,
.slider-arrow-bottom-sm i {
    width: 32px;
    height: 32px;
    margin: 0;
    line-height: 30px;
    font-size: 18px;
    color: #FFF;
    color: rgba(255, 255, 255, 1);
}

.flex-prev i {
    margin-left: 9px;
}

.slider-element .owl-prev i {
    margin-left: 0;
}

.slider-arrow-right i {
    margin-left: 10px;
}

.flex-next i {
    margin-left: 11px;
}

.slider-element .owl-next i {
    margin-right: 4px;
}

.slider-arrow-left:hover,
.slider-arrow-right:hover,
.flex-prev:hover,
.flex-next:hover,
.slider-arrow-top-sm:hover,
.slider-arrow-bottom-sm:hover {
    background-color: rgba(0, 0, 0, 0.6) !important;
}

/* Slide Number
-----------------------------------------------------------------*/

.slide-number {
    position: absolute;
    opacity: 0;
    top: auto;
    right: 20px;
    bottom: 20px;
    z-index: 20;
    width: 36px;
    height: 40px;
    text-align: center;
    color: rgba(255, 255, 255, 0.8);
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
    font-size: 18px;
    font-weight: 300;
    -webkit-transition: all .3s linear;
    transition: all .3s linear;
}

.slider-element:hover .slide-number {
    opacity: 1;
}

.slide-number-current,
.slide-number-total {
    position: absolute;
    line-height: 1;
    top: 5px;
    left: 0;
}

.slide-number-total {
    top: auto;
    bottom: 5px;
    left: auto;
    right: 0;
}

.slide-number span {
    display: block;
    font-size: 32px;
    line-height: 40px;
}

/* Pagination
-----------------------------------------------------------------*/

.slider-element .owl-dots {
    position: absolute;
    width: 100%;
    z-index: 20;
    margin: 0;
    top: auto;
    bottom: 15px;
}

.slider-element .owl-dots button {
    width: 10px;
    height: 10px;
    margin: 0 3px;
    opacity: 1 !important;
    background-color: transparent !important;
    border: 1px solid #FFF;
}

.slider-element .owl-dots .owl-dot.active,
.slider-element .owl-dots .owl-dot:hover {
    background-color: #FFF !important;
}

/* ----------------------------------------------------------------
	Page Title
-----------------------------------------------------------------*/

#page-title {
    position: relative;
    padding: 50px 0;
    background-color: #F5F5F5;
    border-bottom: 1px solid #EEE;
}

#page-title .container {
    position: relative;
}

#page-title h1 {
    padding: 0;
    margin: 0;
    line-height: 1;
    font-weight: 600;
    letter-spacing: 1px;
    color: #333;
    font-size: 28px;
    text-transform: uppercase;
}

#page-title span {
    display: block;
    margin-top: 10px;
    font-weight: 300;
    color: #777;
    font-size: 18px;
}

.breadcrumb {
    position: absolute !important;
    width: auto !important;
    top: 50% !important;
    left: auto !important;
    right: 15px !important;
    margin: -10px 0 0 0 !important;
    background-color: transparent !important;
    padding: 0 !important;
    font-size: 12px;
}

body.stretched .container-fullwidth .breadcrumb {
    right: 60px !important;
}

#header.transparent-header + #page-title.page-title-parallax .breadcrumb {
    margin-top: 35px !important;
}

.breadcrumb a {
    color: #555;
}

.breadcrumb a:hover {
    color: #1ABC9C;
}

.breadcrumb i {
    width: auto !important;
}

/* Page Title - Dark
-----------------------------------------------------------------*/

#page-title.page-title-dark {
    background-color: #333;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15) !important;
    border-bottom: none;
}

#page-title.page-title-dark h1 {
    color: rgba(255, 255, 255, 0.9) !important;
}

#page-title.page-title-dark span {
    color: rgba(255, 255, 255, 0.7);
}

#page-title.page-title-dark .breadcrumb > .active {
    color: rgba(255, 255, 255, 0.6);
}

#page-title.page-title-dark .breadcrumb a {
    color: rgba(255, 255, 255, 0.8);
}

#page-title.page-title-dark .breadcrumb a:hover {
    color: rgba(255, 255, 255, 0.95);
}

/* Page Title - Right Aligned
-----------------------------------------------------------------*/

#page-title.page-title-right {
    text-align: right;
}

#page-title.page-title-right .breadcrumb {
    left: 15px !important;
    right: auto !important;
}

/* Page Title - Center Aligned
-----------------------------------------------------------------*/

#page-title.page-title-center {
    text-align: center;
}

#page-title.page-title-center span {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

#page-title.page-title-center .breadcrumb {
    position: relative !important;
    top: 0 !important;
    left: 0 !important;
    right: 0 !important;
    margin: 20px 0 0 !important;
    justify-content: center;
}

/* Page Title - Background Pattern
-----------------------------------------------------------------*/

#page-title.page-title-pattern {
    background-image: url('/assets/images/pattern.png');
    background-repeat: repeat;
    background-attachment: fixed;
}

/* Page Title - Parallax Background
-----------------------------------------------------------------*/

#page-title.page-title-parallax {
    padding: 100px 0;
    text-shadow: none;
    border-bottom: none;
}

#page-title.page-title-parallax h1 {
    font-size: 40px;
    font-weight: 600;
    letter-spacing: 2px;
}

#page-title.page-title-parallax span {
    font-size: 22px;
}

#page-title.page-title-parallax .breadcrumb {
    font-size: 13px;
}

#page-title.page-title-parallax {
    background-color: transparent;
    background-image: url('/assets/images/parallax/parallax-bg.jpg');
    background-attachment: fixed;
    background-position: 50% 0;
    background-repeat: no-repeat;
}

/* Page Title - Mini
-----------------------------------------------------------------*/

#page-title.page-title-mini {
    padding: 20px 0;
}

#page-title.page-title-mini h1 {
    font-weight: 600;
    font-size: 18px;
    color: #444;
}

#page-title.page-title-mini span {
    display: none;
}

/* Page Title - No Background
-----------------------------------------------------------------*/

#page-title.page-title-nobg {
    background: transparent !important;
    border-bottom: 1px solid #F5F5F5;
}

/* Page Title - Video
-----------------------------------------------------------------*/

#page-title.page-title-video {
    background: none;
    position: relative;
    overflow: hidden;
}

#page-title.page-title-video .container {
    z-index: 3;
}

#page-title.page-title-video .video-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

#page-title.page-title-video .video-wrap video {
    width: 100%;
}

/* ----------------------------------------------------------------
	Content
-----------------------------------------------------------------*/

#content {
    position: relative;
    overflow: hidden;
    background-color: #FFF;
}

#content p {
    line-height: 1.8;
}

.content-wrap {
    position: relative;
    padding: 80px 0;
}

#content .container {
    position: relative;
}

.grid-container {
    position: relative;
    overflow: hidden;
}

body:not(.device-touch) .grid-container {
    -webkit-transition: height .4s ease;
    transition: height .4s ease;
}

/* ----------------------------------------------------------------
	Portfolio
-----------------------------------------------------------------*/

/* Portfolio - Filter
-----------------------------------------------------------------*/

.portfolio-filter {
    position: relative;
    margin: 0 0 40px 0;
    list-style: none;
    border: 1px solid rgba(0, 0, 0, 0.07);
    float: left;
    border-radius: 4px;
}

.portfolio-filter li {
    float: left;
    position: relative;
}

.portfolio-filter li a {
    display: block;
    position: relative;
    padding: 12px 18px;
    font-size: 13px;
    line-height: 15px;
    color: #666;
    border-left: 1px solid rgba(0, 0, 0, 0.07);
}

.portfolio-filter li:first-child a {
    border-left: none;
    border-radius: 4px 0 0 4px;
}

.portfolio-filter li:last-child a {
    border-radius: 0 4px 4px 0;
}

.portfolio-filter li a:hover {
    color: #1ABC9C;
}

.portfolio-filter li.activeFilter a {
    color: #FFF !important;
    background-color: #1ABC9C;
    margin: -1px 0;
    padding: 13px 18px;
    font-weight: bold;
}

.bothsidebar .portfolio-filter li a {
    padding: 12px 14px;
}

.bothsidebar .portfolio-filter li.activeFilter a {
    padding: 13px 18px;
}

/* Portfolio - Filter: Style 2
-----------------------------------------------------------------*/

.portfolio-filter.style-2,
.portfolio-filter.style-3,
.portfolio-filter.style-4 {
    border: none;
    border-radius: 0;
}

.portfolio-filter.style-2 li,
.portfolio-filter.style-3 li,
.portfolio-filter.style-4 li {
    margin-bottom: 10px;
}

.portfolio-filter.style-2 li:not(:first-child),
.portfolio-filter.style-3 li:not(:first-child) {
    margin-left: 10px;
}

.portfolio-filter.style-2 li a,
.portfolio-filter.style-3 li a,
.portfolio-filter.style-4 li a {
    padding-top: 9px;
    padding-bottom: 9px;
    font-size: 14px;
    line-height: 16px;
    border-radius: 22px;
    border: none;
}

.portfolio-filter.style-2 li.activeFilter a {
    color: #FFF !important;
    margin: 0;
}

/* Portfolio - Filter: Style 3
-----------------------------------------------------------------*/

.portfolio-filter.style-3 li a {
    border: 1px solid transparent;
}

.portfolio-filter.style-3 li.activeFilter a {
    color: #1ABC9C !important;
    border-color: #1ABC9C;
    background-color: transparent;
    margin: 0;
}

/* Portfolio - Filter: Style 4
-----------------------------------------------------------------*/

.portfolio-filter.style-4 li:not(:first-child) {
    margin-left: 30px;
}

.portfolio-filter.style-4 li a {
    padding: 13px 5px;
    font-size: 15px;
    border-radius: 0;
}

.portfolio-filter.style-4 li a:after {
    content: '';
    position: absolute;
    top: auto;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.portfolio-filter.style-4 li.activeFilter a {
    color: #444 !important;
    background-color: transparent;
    margin: 0;
}

.portfolio-filter.style-4 li.activeFilter a:after {
    width: 100%;
    left: 0%;
    background-color: #1ABC9C;
}

/* Portfolio - Center Aligned
-----------------------------------------------------------------*/

.portfolio-filter.center.style-2 li,
.portfolio-filter.center.style-3 li,
.portfolio-filter.center.style-4 li {
    display: inline-block;
    float: none;
}

/* Portfolio - Shuffle Icon
-----------------------------------------------------------------*/

.portfolio-shuffle {
    float: right;
    width: 41px;
    height: 41px;
    border: 1px solid rgba(0, 0, 0, 0.07);
    font-size: 14px;
    text-align: center;
    line-height: 41px;
    color: #333;
    border-radius: 4px;
    cursor: pointer;
    -webkit-transition: all .2s linear;
    transition: all .2s linear;
}

.portfolio-shuffle:hover {
    background-color: #1ABC9C;
    color: #FFF;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}

/* Portfolio - Items
-----------------------------------------------------------------*/

.portfolio {
    position: relative;
    margin: 0 -12px -12px 0;
}

body:not(.device-touch) .portfolio {
    -webkit-transition: height .4s linear;
    transition: height .4s linear;
}

.portfolio-item {
