/* Animated Icon Hovers
-----------------------------------------------------------------*/

.fbox-effect .fbox-icon i {
    z-index: 1;
}

.fbox-effect .fbox-icon i:after {
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    content: '';
    box-sizing: content-box;
}

.fbox-rounded.fbox-effect .fbox-icon i:after {
    border-radius: 3px;
}

.fbox-effect .fbox-icon i {
    -webkit-transition: background-color 0.3s, color 0.3s;
    transition: background-color 0.3s, color 0.3s;
}

.fbox-effect .fbox-icon i:after {
    top: -3px;
    left: -3px;
    padding: 3px;
    box-shadow: 0 0 0 2px #333;
    -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
    -webkit-transform: scale(.8);
    -moz-transition: -moz-transform 0.3s, opacity 0.3s;
    -moz-transform: scale(.8);
    -ms-transform: scale(.8);
    -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
    transition: opacity 0.3s, -webkit-transform 0.3s;
    transition: transform 0.3s, opacity 0.3s;
    transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
    transform: scale(.8);
    opacity: 0;
}

.fbox-effect .fbox-icon i:hover,
.fbox-effect:hover .fbox-icon i {
    background-color: #333;
    color: #FFF;
}

.fbox-effect.fbox-dark .fbox-icon i:after {
    box-shadow: 0 0 0 2px #1ABC9C;
}

.fbox-effect.fbox-dark .fbox-icon i:hover,
.fbox-effect.fbox-dark:hover .fbox-icon i {
    background-color: #1ABC9C;
}

.fbox-effect .fbox-icon i:hover:after,
.fbox-effect:hover .fbox-icon i:after {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
}

/* Icon Effects - Bordered
-----------------------------------------------------------------*/

.fbox-border.fbox-effect .fbox-icon i {
    -webkit-transition: color 0.5s, box-shadow 0.5s, background-color 0.5s;
    transition: color 0.5s, box-shadow 0.5s, background-color 0.5s;
}

.fbox-border.fbox-effect .fbox-icon i:after {
    top: -2px;
    left: -2px;
    padding: 2px;
    z-index: -1;
    box-shadow: none;
    background-image: url('/assets/images/icons/iconalt.svg');
    background-position: center center;
    background-size: 100% 100%;
    -webkit-transition: -webkit-transform 0.5s, opacity 0.5s, background-color 0.5s;
    -webkit-transition: opacity 0.5s, background-color 0.5s, -webkit-transform 0.5s;
    transition: opacity 0.5s, background-color 0.5s, -webkit-transform 0.5s;
    transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
    transition: transform 0.5s, opacity 0.5s, background-color 0.5s, -webkit-transform 0.5s;
}

.fbox-border.fbox-rounded.fbox-effect .fbox-icon i:after {
    border-radius: 3px;
}

.fbox-border.fbox-effect .fbox-icon i:hover,
.fbox-border.fbox-effect:hover .fbox-icon i {
    color: #FFF;
    box-shadow: 0 0 0 1px #333;
}

.fbox-border.fbox-effect .fbox-icon i:after {
    -webkit-transform: scale(1.3);
    transform: scale(1.3);
    opacity: 0;
    background-color: #333;
}

.fbox-border.fbox-effect.fbox-dark .fbox-icon i:hover,
.fbox-border.fbox-effect.fbox-dark:hover .fbox-icon i {
    box-shadow: 0 0 0 1px #1ABC9C;
}

.fbox-border.fbox-effect.fbox-dark .fbox-icon i:after {
    background-color: #1ABC9C;
}

.fbox-border.fbox-effect .fbox-icon i:hover:after,
.fbox-border.fbox-effect:hover .fbox-icon i:after {
    -webkit-transform: scale(1);
    transform: scale(1);
