/* ----------------------------------------------------------------
	Clients
-----------------------------------------------------------------*/

.clients-grid,
.testimonials-grid {
    list-style: none;
    overflow: hidden;
}

.clients-grid li,
.testimonials-grid li {
    float: left;
    position: relative;
    width: 20%;
    padding: 20px 0;
    -webkit-backface-visibility: hidden;
}

.clients-grid.grid-2 li {
    width: 50%;
    padding: 25px 0;
}

.clients-grid.grid-3 li {
    width: 33.33%;
}

.clients-grid.grid-4 li {
    width: 25%;
}

.clients-grid.grid-6 li {
    width: 16.66%;
}

.clients-grid li a {
    opacity: 0.6;
}

body:not(.device-touch) .clients-grid li a {
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
}

.clients-grid li a:hover {
    opacity: 1;
}

.clients-grid li a,
.clients-grid li img {
    display: block;
    width: 75%;
    margin-left: auto;
    margin-right: auto;
}

.clients-grid li a img {
    width: 100%;
}

.clients-grid li:before,
.clients-grid li:after,
.testimonials-grid li:before,
.testimonials-grid li:after {
    content: '';
    position: absolute;
}

.clients-grid li:before,
.testimonials-grid li:before {
    height: 100%;
    top: 0;
    left: -1px;
    border-left: 1px dashed #DDD;
}

.clients-grid li:after,
.testimonials-grid li:after {
    width: 100%;
    height: 0;
    top: auto;
    left: 0;
    bottom: -1px;
    border-bottom: 1px dashed #DDD;
}

/* ----------------------------------------------------------------
	Testimonials
-----------------------------------------------------------------*/

/* Testimonials - Grid
-----------------------------------------------------------------*/

.testimonials-grid li {
    width: 50%;
    padding: 25px;
}

.testimonials-grid.grid-3 li {
    width: 33.33%;
}

.testimonials-grid.grid-1 li {
    width: 100%;
}

.testimonials-grid li .testimonial {
    padding: 0;
    background-color: transparent !important;
    border: 0 !important;
    box-shadow: none !important;
}

/* Testimonials - Item
-----------------------------------------------------------------*/

.testimonial {
    position: relative;
    padding: 20px;
    background-color: #FFF;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}

.testi-image {
    float: left;
    margin-right: 15px;
}

.testi-image,
.testi-image a,
.testi-image img,
.testi-image i {
    display: block;
    width: 64px;
    height: 64px;
}

.testi-image i {
    text-align: center;
    background-color: #EEE;
    border-radius: 50%;
    line-height: 64px;
    font-size: 28px;
    color: #888;
    text-shadow: 1px 1px 1px #FFF;
}

.testi-image img {
    border-radius: 50%;
}

.testi-content {
    position: relative;
    overflow: hidden;
}

.testi-content p {
    margin-bottom: 0;
    font-family: 'Crete Round', serif;
    font-style: italic;
}

.testi-content p:before,
.testi-content p:after {
    content: '"';
}

.testi-meta {
    margin-top: 10px;
    font-size: 13px;
    font-weight: bold;
    text-transform: uppercase;
}

.testi-meta:before {
    content: '\2013';
}

.testi-meta span {
    display: block;
    font-weight: normal;
    color: #999;
